import { Injectable } from '@angular/core';
import { HttpClient} from '@angular/common/http';
import { produit } from './modals/product';
import { Observable } from 'rxjs';

@Injectable()
export class ProductService {
 
  constructor(private _http:HttpClient) { }



  deleteProduct(id: number){
    return this._http.delete<produit>('http://localhost:8244/SpringMVC/produit/remove-produit/'+id);
  }
  allproducts():Observable<produit[]>{
    return this._http.get<produit[]>('http://localhost:8244/SpringMVC/produit/retrieve-all-produits/');
  };
}
