import { Component, OnInit, Output,EventEmitter } from '@angular/core';
import { produit } from 'app/modals/product';
import {FormControl, FormGroup, ReactiveFormsModule, Validators } from '@angular/forms';

@Component({
  selector: 'app-addproduct',
  templateUrl: './addproduct.component.html',
  styleUrls: ['./addproduct.component.css']
})
export class AddproductComponent implements OnInit {
product : produit =new produit();
@Output() request= new EventEmitter<produit>();
addform : FormGroup; 
  constructor() { }

  ngOnInit(): void {  
    this.addform=new FormGroup({
      codeproduit : new FormControl('',Validators.compose([Validators.required, Validators.min(100)])),
     libelle: new FormControl('',Validators.compose([Validators.required,Validators.minLength(5)])), 
     prixunitaire : new FormControl('',Validators.compose([Validators.required,Validators.min(100)])),
      });
    }
  
save(){
  this.request.emit(this.product);
}
upload(){

}

}
